@import "styles/variables";

.edit-form {
  text-transform: capitalize;
}

.edit-form__editor {
  height: 300px;
  width: 500px;
  margin-bottom: 1rem;
  padding: 0.5rem;
  font-size: 1.5rem;
  color: $text-gray;
}
.title {
  text-align: left;
  margin: 2rem 2.5rem;
}

.dark__title {
  color: $text-white;
}

.btn {
  font-family: $primary-font;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 28px;
  letter-spacing: -0.02em;
  color: $text-white;
  border-radius: 5px;
  cursor: pointer;
}

.btn-submit {
  background: $bg-blue;
  width: 50%;
  padding: 3px 8px;
  margin: 0.5rem auto;
}

.dark__btn {
  font-family: $primary-font;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 28px;
  letter-spacing: -0.02em;
  color: $text-black;
  border-radius: 5px;
  cursor: pointer;
}

.dark__btn-submit {
  background: $bg-white;
  width: 50%;
  padding: 3px 8px;
  margin: 0.5rem auto;
}
